@import url('https://fonts.googleapis.com/css?family=Comfortaa|Open+Sans');

*{
    margin:0;
    padding:0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
}


::-webkit-scrollbar-track
{
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 10px;
    background-color: rgba(255,255,255,0.1);
}

::-webkit-scrollbar
{
    width: 8px;
    background-color: transparent;
}

::-webkit-scrollbar-thumb
{
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color: rgba(0,0,0,0.6);
}

body{
    background: rgb(244,226,156);
    background: -webkit-linear-gradient(315deg, rgba(244,226,156,0) 0%,rgba(59,41,58,1) 100%), -webkit-linear-gradient(left, rgba(244,226,156,1) 0%,rgba(130,96,87,1) 100%) no-repeat;
    background: -o-linear-gradient(315deg, rgba(244,226,156,0) 0%,rgba(59,41,58,1) 100%), -o-linear-gradient(left, rgba(244,226,156,1) 0%,rgba(130,96,87,1) 100%) no-repeat;
    background: linear-gradient(135deg, rgba(244,226,156,0) 0%,rgba(59,41,58,1) 100%), linear-gradient(to right, rgba(244,226,156,1) 0%,rgba(130,96,87,1) 100%) no-repeat;
    height: 100vh;
    width: 100vw;
}

.heading{
    height:30vh;
    text-align: center;
    line-height: 30vh;
}

.heading h1{
    font-size: 4em;
    font-family: 'Comfortaa', cursive;
}

.login{
    background: rgba(0,0,0,0.5);
    max-width: 500px;
    width: 80vw;
    height: 60vh;
    position: absolute;
    top: 60%;
    left:50%;
    -webkit-transform: translate( -50% , -50% );
        -ms-transform: translate( -50% , -50% );
            transform: translate( -50% , -50% );
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
}

.loginFormSwitcher{
    -ms-flex:1 1 0%;
        flex:1 1 0%;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align:center;
        align-items:center;
}
.loginFormSwitcher button{
    -ms-flex:1 1 0%;
        flex:1 1 0%;
    height: 100%;
    background: rgba(0,0,0,0.3);
    border: none;
    cursor: pointer;
    font-size: 1.5em;
    font-family: 'Comfortaa', cursive;
    color: rgba(255,255,255,0.75);
    outline: none;
}
.loginFormSwitcher button:hover{
    color: #fff;
    background: rgba(0,0,0,0.75);
}
.loginFormWrapper{
    -ms-flex:5 1 0%;
        flex:5 1 0%;
    width: 80%;
}
.signIn, .signUp{
    height: 100%;
    width: 100%;
}

.signIn form, .signUp form{
    height: 100%;
    width:100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    padding: 5vh 0 0;
}

.login input{
    height: 6vh;
    margin: 1vh 0;
    width: 100%;
    border-radius: 100px;
    border: none;
    outline: none;
    padding: 0 3vh;
    font-size: 1.2em;
    background: rgba(255,255,255,0.5);
    -webkit-transition: all 500ms ease;
    -o-transition: all 500ms ease;
    transition: all 500ms ease;
}

.login input:focus{
    background: rgba(255,255,255,0.9);
}


.chat{
    height: 100vh;
    width:100vw;
    display: block;
}

.getPartner{
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate( -50% , -50% );
        -ms-transform: translate( -50% , -50% );
            transform: translate( -50% , -50% );
    max-width: 800px;
    width: 80%;
}

.getPartner h1{
    text-align: center;
    font-family: 'Comfortaa', cursive;
    font-size: 4em;
    line-height: 1.5;
}

.getPartner h2{
    text-align: center;
    font-size: 2em;
    line-height:1.5;
    opacity:0.5;
}

.getPartner form{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align:center;
        align-items:center;
    -ms-flex-direction: column;
        flex-direction: column;
}

.getPartner input{
    height: 6vh;
    margin: 2vh 0;
    width: 75%;
    border-radius: 100px;
    border: none;
    outline: none;
    padding: 0 3vh;
    font-size: 1.2em;
    text-align: center;
    background: rgba(255,255,255,0.5);
    -webkit-transition: all 500ms ease;
    -o-transition: all 500ms ease;
    transition: all 500ms ease;
}

.getPartner input[type="submit"]{
    width:auto;
}


.chatbox{
    height: 80vh;
    width:80vw;
    position: absolute;
    top:50%;
    left: 50%;
    -webkit-transform: translate( -50% , -50% );
        -ms-transform: translate( -50% , -50% );
            transform: translate( -50% , -50% );
    overflow: auto;
}

.chatHeader{
    height: 10vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    background: rgba(255,255,255,0.75);
    padding: 0 25px;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
}
.chatHeader h1{
    font-family: 'Comfortaa', cursive;
}
.chatHeader h2{
    opacity:0.5;
    position: absolute;
    right: 25px;
    cursor: pointer;
}

.chatHeader h2:hover{
    opacity:0.75;
}

.messageContainer{
    height:60vh;
    width: 100%;
    overflow: auto;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
    background: rgba(0,0,0,0.5);

}

.message{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align:center;
        align-items:center;
    padding: 25px 0;
    margin: 10px 20px;
    -ms-flex-pack: end;
        justify-content: flex-end;
    word-wrap: break-word;
}
.message.received{
    -ms-flex-pack: start;
        justify-content: flex-start;
}
.message .icon{
    height:75px;
    width:75px;
    background: white;
    border-radius: 50%;
    margin: 0 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    font-size: 2em;
    font-weight: bold;
}

.message .messageBox{
    max-width: 60%;
    background: coral;
    padding:15px 30px;
    border-radius: 10px;
}

.messageInput{
    width:100%;
    height: 10vh;
}

.messageInput form{
    height: 100%;
    width:100%;
    display: -ms-flexbox;
    display: flex;
    padding: 5px 0 0;
}

.messageInput textarea{
    -ms-flex: 5 1 0%;
        flex: 5 1 0%;
    resize: none;
    padding: 10px 20px;
    font-size: 1.1em;
    border-radius: 1000px;
    outline: none;
    background: rgba(255,255,255,0.75);
}
.messageInput input[type="submit"]{
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
    border-radius: 1000px;
    margin: 0 5px;
    border: none;
    background: rgba(255,255,255,0.75);
    outline: none;
    cursor: pointer;
    font-size: 1.1em;
}
input[type="submit"]:hover{
    background: rgba(255,255,255,1);
    cursor: pointer;
}
input[type="submit"]:active{
    background: rgba(255,255,255,0.75);
    cursor: pointer;
}

.chatHeader .icon{
    background: yellow;
    height: 4vh;
    width: 4vh;
    position: absolute;
    cursor: pointer;
    -webkit-transition: all 300ms ease;
    -o-transition: all 300ms ease;
    transition: all 300ms ease;
}

.chatHeader .icon:hover{
    -webkit-transform:scale(1.05);
        -ms-transform:scale(1.05);
            transform:scale(1.05);
}

.icon.logout{
    right: 25px;
    background: url(./icon/logout.png) center center no-repeat;
    background-size: contain;
}

.icon.change{
    right: calc( 50px + 6vh );
    background: url(./icon/change.png) center center no-repeat;
    background-size: contain;
}

.singles{
    background: hsla(0,0%,100%,.5);
    margin: 0 5px;
    border-radius: 10000px;
    border: none;
    font-size: 1.1em;
    padding: 10px 1em;
    cursor: pointer;
    -webkit-transition: all .3s ease;
    -o-transition: all .3s ease;
    transition: all .3s ease;
}

.waitingSingles{
    width: 80%;
    padding: 0;
    height: calc(20px + 1.1em + 40px);
    overflow-x: auto;
    overflow-y: hidden;
    position: relative;
    -webkit-transform: translate(-50%);
        -ms-transform: translate(-50%);
            transform: translate(-50%);
    left: 50%;
    margin: 10px 0;
    white-space: nowrap;
}

.singles:hover{
    background: rgba(255,255,255,0.75);
    -webkit-transform: scale(1.025);
        -ms-transform: scale(1.025);
            transform: scale(1.025);
}

@media screen and (max-device-width: 480px){
    .loginFormSwitcher{
        width: 100%;
    }
    .loginFormWrapper{
        width:100%;
    }
    .login input{
        font-size:1.1em;
    }
    .getPartner h1{
        font-size:3em;
    }
    .getPartner h2{
        font-size:1.5em;
    }
    .messageInput textarea{
        -ms-flex:3 1 0%;
            flex:3 1 0%;
    }
}




